.Login_container__2ifdT {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
  height: 100vh;
  border: 3px;
  box-sizing: border-box;

  padding-top: 2em;
}
* {
  margin: 0;
  padding: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 80px rgba(0, 0, 0, 0) inset !important;
}

body {
  background: url("https://images.pexels.com/photos/1655166/pexels-photo-1655166.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260")
    no-repeat center center;
  background-size: cover;
  display: grid;
  font-family: "Inconsolata", monospace;
  border: 3px;
  box-sizing: border-box;
}
.Login_holder__3kaQ9 {
  padding-top: 10vh;

  position: relative;
}
.Login_holder__3kaQ9:before,
.Login_holder__3kaQ9:after {
  animation: Login_orbit__XDkox 5s linear infinite;
  border-radius: 50%;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
  content: "";
  height: 150px;
  position: absolute;
  width: 150px;
}
.Login_holder__3kaQ9:before {
  background: #ffe897;
  background: radial-gradient(top left, #ffe897, #f98a05);
  background: -webkit-radial-gradient(top right, #ffe897, #f98a05);
}
.Login_holder__3kaQ9:after {
  animation-delay: -2.5s;
  background: #e0e793;
  background: radial-gradient(to top left, #e0e793, #6dd0f1);
  background: -webkit-radial-gradient(bottom right, #e0e793, #6dd0f1);
  right: 50%;
  top: 50%;
  z-index: -1;
}

.Login_card__1JMI5 {
  border: 1px solid #fff;
  border-radius: 15px;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
  font-size: 1rem;
  height: 160%;
  max-height: 80vw;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
  position: relative;
  width: 370px;
}
.Login_card__1JMI5:before {
  background-color: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px) saturate(100%) contrast(45%) brightness(130%);
  -webkit-backdrop-filter: blur(10px) saturate(100%) contrast(45%)
    brightness(130%);
  content: "";
  height: 100%;
  position: absolute;
  width: 100vw;
  left: -50%;
}
.Login_card__text__2hYQd {
  color: #6f7886;
  margin-left: 10px;
  margin-right: 10px;
  /* margin-top: 100px; */
  position: relative;
  z-index: 2;
}
@keyframes Login_orbit__XDkox {
  from {
    transform: rotate(0deg) translateX(100px) rotate(0deg);
  }
  to {
    transform: rotate(360deg) translateX(100px) rotate(-360deg);
  }
}

